<template>
  <div id="app">
    <audio src="../static/music.mp4" loop class="audio"></audio>
    <audio src="../static/music2.mp3" loop class="audio"></audio>
    <router-view @mute="changeMusic"/>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      audio: null
    }
  },
  mounted () {
    document.addEventListener('DOMContentLoaded', () => {
      let that = this
      function audioAutoPlay () {
        that.audio = document.getElementsByClassName('audio')[0]
        that.audio.play()
        document.addEventListener('WeixinJSBridgeReady', () => {
          that.audio.play()
        }, false)
      }
      audioAutoPlay()
    })
  },
  methods: {
    changeMusic () {
      this.audio.pause()
      this.audio = document.getElementsByClassName('audio')[1]
      this.audio.play()
    }
  }
}
</script>

<style>
  html {
    height: 100%;
    overflow: hidden;
  }
  body {
    margin: 0;
    height: 100%;
    overflow: hidden;
  }
  #app {
    height: 100%;
    overflow: hidden;
  }
</style>



